var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"py-5",attrs:{"id":"dba"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-12 text-center"},[_c('h2',{staticClass:"section-heading text-uppercase"},[_vm._v("Administrateur de base de données")]),_c('h3',{staticClass:"section-subheading text-muted"},[_vm._v("Métier d'administration et de gestion de base de données.")])])]),_c('div',{staticClass:"container"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-3 col-sm-6"},[_c('a',{attrs:{"href":"#"}},[_c('img',{staticClass:"img-fluid d-block mx-auto",attrs:{"src":"img/mysql.jpeg","alt":"mysql"}})])]),_c('div',{staticClass:"col-md-3 col-sm-6"},[_c('a',{attrs:{"href":"#"}},[_c('img',{staticClass:"img-fluid d-block mx-auto",attrs:{"src":"img/oracle.jpg","alt":"oracle"}})])]),_c('div',{staticClass:"col-md-3 col-sm-6"},[_c('a',{attrs:{"href":"#"}},[_c('img',{staticClass:"img-fluid d-block mx-auto",attrs:{"src":"img/mariadb.jpg","alt":"mariadb"}})])]),_c('div',{staticClass:"col-md-3 col-sm-6"},[_c('a',{attrs:{"href":"#"}},[_c('img',{staticClass:"img-fluid d-block mx-auto",attrs:{"src":"img/sqlite.jpg","alt":"sqlite"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-12 text-center adminres"},[_c('p',{staticClass:"text-muted"},[_vm._v("\n\t\t\t\tUn administrateur de base de données est une personne responsable du bon fonctionnement de serveurs de base de données, essentiellement relationnelles (OLTP) ou décisionnelles (OLAP), tant au niveau de la conception \n\t\t\t\tdes bases, des tests de validation, de la coordination des intervenants, de l'exploitation, de la protection et du contrôle d'utilisation. Les compétences requises pour cette fonction sont \n\t\t\t\tmultipolaires : système, développement, sécurité et fonctionnement des serveurs de base de données. L'expérience y est prépondérante. Le DBA (DataBase Administrator) travaille souvent en relation étroite avec les administrateurs \n\t\t\t\tsystèmes et les développeurs au sein d'une direction des systèmes d'information.\n\t\t\t\t")])]),_c('div',{staticClass:"col-lg-12 text-center adminres"},[_c('h2',{staticClass:"section-heading text-uppercase"},[_vm._v("Tâches de l'administrateur")]),_c('p',{staticClass:"text-muted paragraphs-tech"},[_c('ul',{staticClass:"list-group"},[_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tLa mise en place de standards, préconisation et bonnes pratiques : il décide des normes de nommage \n\t\t\t\t\t\tdes objets pour les bases créées en interne, édicte les bonnes pratiques que les développeurs \n\t\t\t\t\t\tdevront suivre, documente les procédures de maintenance afin que d'autres acteurs puissent intervenir\n\t\t\t\t\t\ten son absence.\t\t\t\t\t\t\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tL'évaluation des besoins et de la qualité : certaines bases de données n'étant pas développées de manière interne,\n\t\t\t\t\t\til est important qu'il soit consulté afin de dimensionner les serveurs devant héberger une solution applicative d'\n\t\t\t\t\t\téditeur, tant sur le plan des ressources (volumétrie, nombre d'accès), que sur le plan de la maintenance \n\t\t\t\t\t\t(maintien des performances, fréquence des sauvegardes…).\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tLa modélisation de la base : bien qu'elle incombe normalement à l'analyste ou au concepteur (parfois\n\t\t\t\t\t\tau développeur), les principes de modélisation de bases de données doivent être parfaitement maitrisés\n\t\t\t\t\t\tpar le DBA. En effet, les facteurs de dégradation des performances étant en premier lieu liés à la \n\t\t\t\t\t\tstructuration des données (respects des principes de modélisation, relationnel ou décisionnel…), \n\t\t\t\t\t\tle DBA se doit de bien connaître les principes de modélisation et les problématiques en jeu afin de\n\t\t\t\t\t\tconseiller les analystes et développeurs ou bien pour résoudre les problèmes de performance à ce \n\t\t\t\t\t\tniveau par refactoring.\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tLa définition et la gestion des espaces de stockage : comme une base de données a besoin de beaucoup\n\t\t\t\t\t\tde place pour les données et le journal de transaction, il doit dimensionner les espaces de stockage\n\t\t\t\t\t\tphysiques (storage : tablespace, groupes de fichiers…) et logiques (partitionnement), et choisir les \n\t\t\t\t\t\tdisques ou SAN de manière préventive, et les auditer régulièrement afin de décider de l'ajout ou du \n\t\t\t\t\t\tbasculement de certains objets logiques sur de nouveaux espaces (croissance de la volumétrie des \n\t\t\t\t\t\tdonnées et \"capacity planning\").\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tL'intégrité des données : il vérifie ou aide à vérifier la cohérence des données de la base afin qu'\n\t\t\t\t\t\telles ne rentrent pas en conflit avec les principes du système réel. Pour cela, il est chargé de la\n\t\t\t\t\t\tmise en œuvre de contraintes d'intégrité (intégrité de domaine, clef primaires et subrogées, clefs \n\t\t\t\t\t\tétrangères et leur mode de gestion, validation des données, assertions…).\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tLa sécurité d'accès aux données : il définit ou implémente l'accès aux données en autorisant des\n\t\t\t\t\t\tprofils de connexion ayant faculté d'exécuter certaines commandes dans certaines bases (gestion des \n\t\t\t\t\t\tprivilèges).\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tLa récupération de données : il doit s'assurer que le plan de sauvegarde mis en place est opérationnel et \n\t\t\t\t\t\trecouvrant. Pour ce faire il doit auditer la fréquence de changement des données sensibles afin, par ses \n\t\t\t\t\t\tsauvegardes, de pouvoir remonter une base à un point particulier du calendrier, à la suite d'une erreur \n\t\t\t\t\t\tfonctionnelle. Il doit aussi et très régulièrement vérifier la consistance des sauvegardes et la faisabilité\n\t\t\t\t\t\tde la restauration en expérimentant celle-ci dans des conditions proches de la réalité (gestion de \n\t\t\t\t\t\tdésastre).\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tLa maintenance de base : il doit vérifier que les espaces de stockage sont en bon état et dans le cas contraire les réparer. \n\t\t\t\t\t\tIl doit défragmenter les structures de stockage et les index afin d'assurer un temps de réponse linéaire. Il doit s'assurer de\n\t\t\t\t\t\tla bonne gestion des fichiers (données et journaux de transactions) afin que ces derniers ne saturent pas les disques.\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tLa gestion des désastres (disaster recovery) : il doit créer et tester des solutions de maintien de la continuité de la \n\t\t\t\t\t\tproduction (clusterisation, mise en miroir, log shipping…) afin qu'en cas de sinistre matériel la production puisse redémarrer \n\t\t\t\t\t\tdans un temps imparti prédéfini (quelques secondes pour les meilleures solutions);\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tLe maintien de la performance : il assure que l'accès aux données se fasse dans un temps raisonnable et que cette performance \n\t\t\t\t\t\tsoit maintenue dans le temps. Il doit donc mettre en œuvre une veille sur les statistiques d'exécution et réagir sur des \n\t\t\t\t\t\tallongements de temps de réponse en diagnostiquant le problème et en le résolvant si celui-ci est de son ressort (procédures \n\t\t\t\t\t\tde maintenance, ajout de ressources physique, refactoring du modèle de données…). Pour cela il dispose d'outils qui lui \n\t\t\t\t\t\tdonnent des informations sur les objets les plus utilisés et leur consommation de ressource lors des traitements (lecture, \n\t\t\t\t\t\técritures, temps CPU…).\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tL'optimisation : il doit régulièrement et de manière pro-active faire des campagnes de mesure afin de débusquer les problèmes\n\t\t\t\t\t\tde contention ou de temps de réponse avant qu'ils ne deviennent handicapants pour l'exploitation et proposer les mesures \n\t\t\t\t\t\tnécessaires à les éradiquer : meilleurs gestion des transactions, études d'indexation, statistiques, réécritures de requêtes.\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tL'aide au développement et aux tests : il doit fournir un support aux équipes de développement. Par exemple, il peut fournir \n\t\t\t\t\t\tdes jeux de données en vue de tests (pris sur des bases en production), conseiller les développeurs sur les techniques à \n\t\t\t\t\t\tutiliser afin d'obtenir les meilleures performances d'emblée, voir participer à la structuration de la base afin notamment \n\t\t\t\t\t\td'implémenter les nouvelles fonctions. En dernier ressort, il valide les demandes de modifications ou modification du schéma \n\t\t\t\t\t\tde la base.\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tLa gestion des flux de données : il est responsable des problématiques d'import et d'export des données tant sur le plan des ressources à y allouer \n\t\t\t\t\t\tque de la sécurité à mettre en œuvre.\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tMigration et mises à jour : il doit régulièrement appliquer les mises à jour préconisées (système et SGBD) et assurer la migration des bases par exemple dans\n\t\t\t\t\t\tle cas d'un changement de serveur.\n\t\t\t\t\t\t")])]),_vm._v("\n\t\t\t\t\t\n\t\t\t\t\tOn distingue plusieurs sous-catégories de DBA : \n\t\t\t\t\t\n\t\t\t\t\t"),_c('ul',{staticClass:"list-group"},[_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tL'administrateur de bases de données développement ou études : Il travaille conjointement avec les équipes de développement et est \n\t\t\t\t\t\tresponsable du modèle logique et physique de la base de données. Il a souvent à charge l'administration des bases pendant la durée \n\t\t\t\t\t\tdu projet. Il peut être amené à développer les procédures ou scripts SQL, et participe à la validation et aux tests. Il est très \n\t\t\t\t\t\tsouvent responsable du contenu et du contenant (les données et le serveur lui-même).\t\t\t\t\n\t\t\t\t\t\t")]),_c('li',{staticClass:"list-group-item"},[_vm._v("\n\t\t\t\t\t\tL'administrateur de bases de données production : Il assure la disponibilité et le bon fonctionnement des systèmes de bases de \n\t\t\t\t\t\tdonnées. Il travaille en environnement haute disponibilité 24h/24h 7/7j. Cette fonction s'accompagne presque systématiquement \n\t\t\t\t\t\td'astreintes. La volumétrie peut ici être très importante, quelques dizaines à quelques milliers de bases de données par DBA,\n\t\t\t\t\t\tsoit au maximum une centaine de serveurs à surveiller. Il est en général responsable du contenant mais pas du contenu : \n\t\t\t\t\t\tle système de bases de données, mais pas les données elles-mêmes. Cependant il doit toujours être en mesure de récupérer \n\t\t\t\t\t\tles données, et de restaurer une image valide des systèmes. Il est mieux rémunéré que l'administrateur développement \n\t\t\t\t\t\tprincipalement à cause des risques et des responsabilités qu'il assume : une panne majeure pouvant aller jusqu'à la faillite\n\t\t\t\t\t\tde la compagnie.\n\t\t\t\t\t\t")])])])]),_c('div',{staticClass:"col-lg-12 text-center adminres"},[_c('h2',{staticClass:"section-heading text-uppercase"}),_c('p',{staticClass:"text-muted paragraphs-tech"})]),_c('div',{staticClass:"col-lg-12 text-center adminres"},[_c('h2',{staticClass:"section-heading text-uppercase"}),_c('p',{staticClass:"text-muted paragraphs-tech"})])])]),_c('hr')])}]

