odèles de solutions déjà \n\t\t\t\t\t\t\t\téprouvés. Il en résulte des diagrammes d'architecture, une description du modèle de données et le diagramme de classes, les diagrammens utilisent \n\t\t\t\t\t\t\t\tsouvent la notation UML.\n\t\t\t\t\t\t\t\t")])])])]),_c('li',[_c('div',{staticClass:"timeline-image"}),_c('div',{staticClass:"timeline-panel"},[_c('div',{staticClass:"timeline-heading"},[_c('h4',[_vm._v("Construction")])]),_c('div',{staticClass:"timeline-body"},[_c('p',{staticClass:"text-muted"},[_vm._v("\n\t\t\t\t\t\t\t\tLa construction est la principale activité, qui occupe plus de la moitié de la durée totale du développement. Durant cette étape, le ou les programmes rédigent \n\t\t\t\t\t\t\t\tle code source du logiciel, puis utilisent un compilateur pour le transformer et obtenir les différents composants du logiciel proprement dit. Le programmeur effectue \n\t\t\t\t\t\t\t\tégalement de nombreux tests en vue de déceler le maximum de bugs, des erreurs de programmation. Des test de non-régression permettent de vérifier que l'ajout de nouvelles \n\t\t\t\t\t\t\t\tfonctionnalités ne compromet pas les anciennes. L'un des soucis du procédé est que des tests peuvent démontrer la présence d'erreurs mais jamais leur absence. Les différents \n\t\t\t\t\t\t\t\tmodules du logiciel sont programmés un par un, puis rassemblés, et de nouveaux tests sont effectués en vue de vérifier le fonctionn de l'ensemble : test d'intégration. Le produit \n\t\t\t\t\t\t\t\tde cette phase est livrable : l'ensemble des fichiers mis à disposition de l'utilisateur final, non seulement les programmes mais le mode d'emploi et les fichiers de configuration.\n\t\t\t\t\t\t\t\tLe livrable peut être une application informatique complète, un composant ou un patch.\n\t\t\t\t\t\t\t\t")])])])]),_c('li',{staticClass:"timeline-inverted"},[_c('div',{staticClass:"timeline-image"}),_c('div',{staticClass:"timeline-panel"},[_c('div',{staticClass:"timeline-heading"},[_c('h4',[_vm._v("Recette")])]),_c('div',{static